html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "FaktPro";
  font-style: normal;
  font-weight: 400;
  src: local("FaktPro Normal"), local("FaktPro-Normal"), url("../fonts/FaktPro-Normal.woff2") format("woff2"), url("../fonts/FaktPro-Normal.woff") format("woff");
}

.icon-arrow {
  width: 0.93em;
  height: 1em;
  fill: currentColor;
}

.icon-logo {
  width: 3.53em;
  height: 1em;
  fill: currentColor;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
  font-family: "FaktPro";
  font-size: 20px;
  color: #000;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.wrap {
  position: absolute;
  width: 100%;
  height: 100%;
}

.ds-parent {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.ds-container {
  position: absolute;
  width: 100%;
  height: 100% !important;
  overflow: hidden;
  z-index: 100;
  transform: none !important;
}

.ds-container > * {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.slide-in {
  animation: fade-out 4.5s;
}

.slide-out {
  animation: fade-in 1.5s;
}

@keyframes fade-out {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

h2 {
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

nav {
  position: absolute;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100% !important;
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content.active {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  padding-list-style-type: none;
}

.row {
  padding: 50px 0 !important;
}

.container-inner {
  width: 100%;
  padding: 0 36px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.row > [class*="col"]:first-child {
  padding-left: 0;
}

.row > [class*="col"]:last-child {
  padding-right: 0;
}

.col1 {
  flex: 0 0 8.33333%;
}

.col2 {
  flex: 0 0 16.66667%;
}

.col3 {
  flex: 0 0 25%;
}

.col4 {
  flex: 0 0 33.33333%;
}

.col5 {
  flex: 0 0 41.66667%;
}

.col6 {
  flex: 0 0 50%;
}

.col7 {
  flex: 0 0 58.33333%;
}

.col8 {
  flex: 0 0 66.66667%;
}

.col9 {
  flex: 0 0 75%;
}

.col10 {
  flex: 0 0 83.33333%;
}

.col11 {
  flex: 0 0 91.66667%;
}

.col12 {
  flex: 0 0 100%;
}

.col-24 {
  flex: 0 0 24%;
}

.col-32 {
  flex: 0 0 32%;
}

.col-44 {
  flex: 0 0 44%;
}

.header {
  position: absolute;
  top: 42px;
  width: 100%;
}

.nav {
  width: 100%;
}

.nav__group > li:first-child {
  margin-right: 60px;
}

.nav__item {
  display: inline-block;
  list-style-type: none;
}

.toggler-container {
  position: relative;
}

.menu-trigger {
  position: absolute;
  top: 0;
  right: 0;
}

.menu-trigger span {
  display: inline-block;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #000;
}

.footer {
  position: absolute;
  bottom: 42px;
  left: 0;
  width: 100%;
  transform: translate3d(0, 0, 0);
}
