body {
    @include font-smoothing;
    min-width: 320px;
    font-family: $faktPro;
    font-size: 20px;
    color: #000; }

body {
    margin: 0;
    padding: 0;
    overflow: hidden; }

.wrap {
    position: absolute;
    width: 100%;
    height: 100%; }

.ds-parent {
    margin: 0;
    padding: 0;
    overflow: hidden; }



.ds-container {
    position: absolute;
    width: 100%;
    height: 100% !important;
    overflow: hidden;
    z-index: 100;
    transform: none !important; }


.ds-container > * {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
 }    // transition: 0.3s opacity

.slide-in {
    animation: fade-out 4.5s; }

.slide-out {
    animation: fade-in 1.5s; }

@keyframes fade-out {
    from {opacity: 0};
    to {opacity: 1}; }

@keyframes fade-in {
    from {opacity: 1};
    to {opacity: 0}; }

h2 {
    position: absolute;
    @include vcenter; }

nav {
    position: absolute; }

.content {
    position: absolute;
    // position: static
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    opacity: 0;
    visibility: hidden;
    z-index: 1;
    transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
    @include vcenter; }

.content.active {
    opacity: 1;
    visibility: visible;
    z-index: 1; }

img {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

ul {
	margin: 0;
	padding: 0 {
    list-style-type: none; } }

.row {
    padding: 50px 0 !important; }
