.header {
    position: absolute;
    top: 42px;
    width: 100%; }


.nav {
    width: 100%;
    &__group > li:first-child {
        margin-right: 60px; }
    &__item {
        display: inline-block;
        list-style-type: none; } }

.toggler-container {
    position: relative; }

.menu-trigger {
    position: absolute;
    top: 0;
    right: 0;
    span {
        display: inline-block;
        height: 6px;
        width: 6px;
        border-radius: 50%;
        background-color: #000; } }

