@mixin col($column, $sum, $gap: 10px, $align: top, $first: false, $last: false) {
  flex: 0 0 percentage($column / $sum); }
//   font-size: 24px
//// display: inline-block
//   vertical-align: $align
//box-sizing: border-box
//padding-left: if($first, 0, $gap)
//padding-right: if($last, 0, $gap)

.container-inner {
    width: 100%;
    padding: 0 36px;
    margin: 0 auto; }

.row {
    display: flex;
    flex-wrap: wrap; }

.row>[class*="col"]:first-child {
    padding-left: 0; }

.row>[class*="col"]:last-child {
    padding-right: 0; }

.col1 {
    @include col(1,12); }
.col2 {
    @include col(2,12); }
.col3 {
    @include col(3,12); }
.col4 {
    @include col(4,12); }
.col5 {
    @include col(5,12); }
.col6 {
    @include col(6,12); }
.col7 {
    @include col(7,12); }
.col8 {
    @include col(8,12); }
.col9 {
    @include col(9,12); }
.col10 {
    @include col(10,12); }
.col11 {
    @include col(11,12); }
.col12 {
    @include col(12,12); }

.col-24 {
    flex: 0 0 24%; }

.col-32 {
    flex: 0 0 32%; }

.col-44 {
    flex: 0 0 44%; }
